<template>
    <div>
        <div class="d-block header-section">
            <div class="float-left"> <h1><b>Product Management</b></h1></div>
            <div class="float-right"><button @click="loadNewProductScreen()" class="btn btn-link add-product"><img src="@/assets/images/icons/add.png" alt="">NEW PRODUCT</button></div>
        </div>
        <!-- Add New Product-->
        <b-modal id="addNewProduct" hide-footer centered>
            <template v-slot:modal-title>
                Add New Product
            </template>
            <div class="d-block">
                <div class="info-coil">
                    <form>
                        <div class="form-row">
                            <div class="col-sm-4">
                                <div class="col-sm-12">
                                    <div class="row form-group" :class="{ 'form-group--error': $v.productAddition.newProductName.$error }">
                                        <label class="p-2">Product</label>
										<div class="col-sm-9">
											<input type="text" class="form-control" placeholder="Product Name" v-model="$v.productAddition.newProductName.$model" />
											<div v-if="addProductErros">
												<span class="text-danger" v-if="!$v.productAddition.newProductName.required">Product name is required</span>
												<span class="text-danger" v-else-if="!$v.productAddition.newProductName.minLength">Product name must have altleast {{$v.productAddition.newProductName.$params.minLength.min}} letters</span>
												<!-- <span class="text-danger" v-else-if="!$v.productAddition.newProductName.checkDuplicateName">Product name already exist</span> -->
											</div>
										</div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="col-sm-12">
                                    <div class="row form-group" :class="{ 'form-group--error': $v.productAddition.selectedOrgin.$error }">
                                        <label class="p-2">Origin</label>
										<div class="col-sm-9">
											<select class="form-control" v-model="productAddition.selectedOrgin">
												<option value="">Select Orgin</option>
												<option v-for="orgin in this.$store.state.orgins" :value="orgin" :key="orgin">{{orgin}}</option>
											</select>
											<div v-if="addProductErros">
												<span class="text-danger" v-if="!$v.productAddition.selectedOrgin.required">Orgin is required</span>
												<span class="text-danger" v-else-if="!$v.productAddition.selectedOrgin.checkValid">Select valid Orgin</span>
											</div>
										</div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-4">                                
                                <div class="col-sm-12">
                                    <div class="row form-group" :class="{ 'form-group--error': $v.productAddition.selectedCategory.$error }">
										<label class="p-2" for="category">Category</label>
										<div class="col-sm-9">
											<select class="form-control" id="selCategory" v-model="productAddition.selectedCategory">
												<option value= null>Select Category</option>
												<template v-for="category in  this.$store.state.categories">
													<option :value="category" :key="category.id">{{category.name}}</option>
												</template>
											</select>
											<div v-if="addProductErros">
												<span class="text-danger" v-if="!$v.productAddition.selectedCategory.required">Category is required</span>
												<span class="text-danger" v-else-if="!$v.productAddition.selectedCategory.checkValid">Select valid Category</span>
											</div>
										</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <hr class="dot" />
                        <h6 class="mb-4">
                            Reel Size
                            <button class="btn float-right btn-link" type="button" v-on:click="addReelSizeErrors=false;" v-b-toggle.addreel1><img src="@/assets/images/icons/add-small.png" /> Reel Size</button>
                        </h6>
                        <div class="col-md-12" v-for="reelSize in getAllReelSizes" :key="reelSize.reelSizeId">
                            <div class="col-sm-3 float-left">
                                <label class="switch">
                                    <input type="checkbox" :id="'reel_'+reelSize.id" v-model="reelSize.isReelSizeSelected" />
                                    <div class="slider round"></div>
                                    <span>{{reelSize.size}}</span>
                                </label>
                            </div>
                            <div class="col-sm-5 float-left">
                                <label class="switch">
                                    <input type="checkbox" :id="'reelMf_'+reelSize.id" v-model="reelSize.canMfOrder" />
                                    <div class="slider round"></div>
                                    <span>Visible to Manufacturer</span>
                                </label>
                            </div>
                            <div class="col-sm-4 float-left">
                                <label class="switch">
                                    <input type="checkbox" :id="'reelTpsaf_'+reelSize.id" v-model="reelSize.canTpsafOrder" />
                                    <div class="slider round"></div>
                                    <span>Visible to TPSAF</span>
                                </label>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <b-collapse id="addreel1" class="mt-2">
                            <div class="form-row mt-4 green-col">
                                <div class="form-group row mt-3 col-md-5">
                                    <label for="newReelsize" class="p-2 col-form-label text-dark">Reel Size</label>
                                    <div class="col-sm-9">
                                        <div class="form-group" :class="{ 'form-group--error': $v.newReelSize.$error }">
                                            <input type="number" class="form-control" id="newReelsize" placeholder="Reel Size" v-model.number="newReelSize" />
                                            <div v-if="addReelSizeErrors">
                                                <span class="text-danger" v-if="!$v.newReelSize.required">ReelSize is required</span>
                                                <span class="text-danger" v-else-if="!$v.newReelSize.checkNaN">Reel Size is required</span>
                                                <span class="text-danger" v-else-if="!$v.newReelSize.checkValid">ReelSize should have above 2500 </span>
                                                <span class="text-danger" v-else-if="!$v.newReelSize.checkDuplicateSize">ReelSize already exist</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-7 mt-3 mb-3 p-0 text-right">
                                    <img src="@/assets/images/icons/tick.png" type="button" @click="addNewReelSize();" />
                                    <img src="@/assets/images/icons/cross-red.png" v-b-toggle.addreel1 />
                                </div>
                            </div>
                        </b-collapse>
                        <hr class="dot" />
                        <h6 class="mb-4">
                            SKU <button class="btn float-right btn-link" type="button" v-on:click="addSKuErrors=false;" v-b-toggle.addsku1><img src="@/assets/images/icons/add-small.png" />SKU</button>
                        </h6>
                        <div class="col-md-12" v-for="sku in getAllSKUs" :key="sku.id">
                            <div class="col-md-2 float-left">
                                <label class="switch">
                                    <input type="checkbox" :id="'sku_'+sku.id" v-model="sku.isSKUSelected" />
                                    <div class="slider round"></div>
                                    <span>{{sku.unit}}</span>
                                </label>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <b-collapse id="addsku1" class="mt-2">
                            <div class="form-row mt-4 green-col">
                                <div class="form-group row mt-3 mb-3 col-sm-5">
                                    <label for="newDesc" class="p-2 col-form-label text-dark">Description</label>
                                    <div class="col-sm-9">
                                        <div class="form-group" :class="{ 'form-group--error': $v.newDescribtion.$error }">
											<textarea class="form-control" id="newDesc" placeholder="Description" v-model="newDescribtion" rows="3"></textarea>
                                            <p v-if="addSKuErrors"> <span class="text-danger" v-if="!$v.newDescribtion.required">Description is required</span></p>
                                        </div>
                                    </div>
                                </div>
                                <div class="form-group row mt-3 col-sm-5">
                                    <label for="newUnit" class="p-2 col-form-label text-dark">Unit</label>
                                    <div class="col-sm-9">
                                        <div class="form-group" :class="{ 'form-group--error': $v.newUnit.$error }">
                                            <input type="text" class="form-control" id="newUnit" placeholder="Unit" v-model="newUnit" />
                                        </div>
                                        <p v-if="addSKuErrors">
                                            <span class="text-danger" v-if="!$v.newUnit.required">Unit is required</span>
                                            <span class="text-danger" v-else-if="!$v.newUnit.checkDuplicateUnit">Unit already exist</span>
                                        </p>
                                    </div>
                                </div>
                                <div class="col-sm-2 mt-3 text-right">
                                    <img src="@/assets/images/icons/tick.png" @click="addNewSKU()" />
                                    <img src="@/assets/images/icons/cross-red.png" v-b-toggle.addsku1 />
                                </div>
                            </div>
                        </b-collapse>
                        <hr class="dot" />
                        <h6>
                            Stamp Price <button class="btn float-right btn-link" v-if="isAddPriceEnabled" type="button" v-on:click="addPriceErrors=false;" v-b-toggle.addprice1><img src="@/assets/images/icons/add-small.png" />PRICE</button>
                        </h6>
                            <div class="form-row mt-4" v-if="productAddition.arrPriceList.length!=0">
                                <div class="table-responsive">
                                    <table class="table layout-secondary bordered">
                                        <thead>
                                            <tr>
                                                <th>Stamp Price in GH</th>
                                                <th>Start Date</th>
                                                <th>End Date</th>
                                                <th>Status</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="prices in productAddition.arrPriceList" :key="prices.price">
                                                <td scope="row">{{prices.price}}</td>
                                                <td>{{prices.effectiveDate | FormatingDate }}</td>
                                                <td>{{prices.endDate | FormatingDate}}</td>
                                                <td class="text-success" v-if="prices.status==1">Active</td>
                                                <td class="text-primary" v-else-if="prices.status==2">Enqueued</td>
                                                <td class="text-danger" v-else>Obsolete</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        
                        <b-collapse id="addprice1" class="">
                            <div class="form-row green-col" v-if="isAddPriceEnabled">
                                <div class="form-group row mt-3 col-md-5">
                                    <label for="newPrice" class="p-2 col-form-label text-dark">Price</label>
                                    <div class="col-sm-8">
										<div class="input-group" :class="{ 'form-group--error': $v.newPrice.$error }">
											<input type="number" class="form-control" id="newPrice" placeholder="Price" v-model.number="newPrice" />
											<div class="input-group-append">
												<div class="input-group-text">GH</div>
											</div>
                                            <p v-if="addPriceErrors">
                                                <span class="text-danger" v-if="!$v.newPrice.required">Price is required</span>
                                                <span class="text-danger" v-else-if="!$v.newPrice.checkValid">This price should be above 0</span>
                                            </p>
										</div>
                                    </div>
                                </div>

                                <div class="form-group row mt-3 mb-3 col-md-5">
                                    <label for="newEffectiveDate" class="p-2 col-form-label text-dark">Start Date</label>
                                    <div class="col-md-8 p-0">
                                        <div class="form-group" :class="{ 'form-group--error': $v.newPrice.$error }">                                    
											<b-form-datepicker id="newEffectiveDate" right v-model="newEffectiveDate" placeholder="DD-MM-YYYY" :min="minDate" :date-format-options="{ year: 'numeric', month: 'numeric', day: 'numeric'}" locale="en"></b-form-datepicker>
                                            <p v-if="addPriceErrors"><span class="text-danger" v-if="!$v.newEffectiveDate.required">Start Date is required</span></p>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-2 mt-3 text-right">
                                    <img src="@/assets/images/icons/tick.png" v-on:click="addNewPrice()" />
                                    <img src="@/assets/images/icons/cross-red.png" v-b-toggle.addprice1 />
                                </div>
                            </div>
                        </b-collapse>
                        <hr class="dot" />
                        <h6 class="mb-4">Choose Product Image</h6>
                        <vue-select-image :dataImages="dataImages"
                            @onselectimage="onSelectImage">
                        </vue-select-image>
                        <!-- <p v-if="addImageErros"><span class="text-danger">select product image</span></p> -->
                    </form>
                </div>
            </div>
            <div class="row modal-footer">
                <button type="button" class="btn btn-lg btn-outline-primary" v-on:click="$bvModal.hide('addNewProduct');">Cancel</button>
                <button type="button" class="btn btn-lg btn-primary" v-on:click="addNewProduct()">Save</button>
            </div>
        </b-modal>
        <!-- End Add Model-->
        <div class="productManagement">
            <!-- START: Card Data-->
            <div v-for="product in getAllProducts" :key="product.id" class="w-100">
                <b-card no-body>
                    <b-card-header header-tag="header" type="button" class="p-1" :id="'header-' + product.id" :class="'accordion-' + product.id" :key="'accordion-' + product.id" aria-controls="reel1 sku1" v-on:click="toggleAccordianBasedOnProduct(product);">
                        <table class="table">
							<tbody>
								<tr>
									<td class="td-mt wid-100">{{product.id}}</td>
									<td class="td-mt wid-350">
										<img :src = "getImageUrl(product.imageName)"  />
										<label v-if="product.origin==1">{{product.name + ' Domestic'}}</label>
										<label v-else>{{product.name + ' Imported'}}</label>
									</td>
									<td class="td-mt" v-if="(product.priceHistories.filter(s=>s.status==1)).length!=0">
										<span class="text-primary mr-3"> Current Price :</span> GH
										<template v-for="prodPrices in product.priceHistories.filter(s=>s.status==1)">
											<span class="text-dark ml-2" :key="prodPrices.id">{{prodPrices.price}}</span>
										</template>
									</td>
									<td class="td-mt" v-else>
									</td>
									<td class="float-right td-mt">
										<img src="@/assets/images/icons/down-arrow.png" />
									</td>
								</tr>
							</tbody>
						</table>
                    </b-card-header>
                    <b-collapse :id="'accordion-' + product.id" accordion="my-accordion" role="tabpanel">
                        <b-card-body>
                            <div id="collapseOne" aria-labelledby="headingOne" data-parent="#accordion">
                                <div class="p-3 ml-5 mr-5 p-20">
                                    <form>
                                        <h6 class="mb-4">
                                            Reel Size
                                            <button class="btn float-right btn-link" type="button" v-on:click="addNewReelSizeErrorsforProduct=false" v-b-toggle.reel1><img src="@/assets/images/icons/add-small.png" /> Reel Size</button>
                                        </h6>
                                        <div class="col-md-12" v-for="reelsize in getAllReelsforEditProduct" :key="'eR' + reelsize.size">
                                            <div class="col-sm-3 float-left">
                                                <label class="switch">
                                                    <input type="checkbox" v-model="reelsize.isActive" />
                                                    <div class="slider round"></div>
                                                    <span>{{reelsize.size}}</span>
                                                </label>
                                            </div>
                                            <div class="col-sm-5 float-left">
                                                <label class="switch">
                                                    <input type="checkbox" v-model="reelsize.canMfOrder" />
                                                    <div class="slider round"></div>
                                                    <span>Visible to Manufacturer</span>
                                                </label>
                                            </div>
                                            <div class="col-sm-4 float-left">
                                                <label class="switch">
                                                    <input type="checkbox" v-model="reelsize.canTpsafOrder" />
                                                    <div class="slider round"></div>
                                                    <span>Visible to TPSAF</span>
                                                </label>
                                            </div>
                                        </div>
                                        <div class="clearfix"></div>
                                        <b-collapse id="reel1" v-model="isVisiblereel1" class="mt-2">
                                            <div class="form-row mt-4 green-col">
                                                <div class="form-group row mt-3 col-md-4">
                                                    <label class="p-2 col-form-label text-dark">Reel Size</label>
                                                    <div class="col-sm-9">
                                                        <div class="form-group" :class="{ 'form-group--error': $v.objNewReelSize.size.$error }">
                                                            <input type="number" class="form-control" placeholder="Reel Size" v-model.number="objNewReelSize.size" />
                                                            <div v-if="addNewReelSizeErrorsforProduct">
                                                                <span class="text-danger" v-if="!$v.objNewReelSize.size.required">ReelSize is required</span>
                                                                <span class="text-danger" v-else-if="!$v.objNewReelSize.size.checkNaN">ReelSize is required </span>
                                                                <span class="text-danger" v-else-if="!$v.objNewReelSize.size.checkValid">ReelSize should have above 2500 </span>
                                                                <span class="text-danger" v-else-if="!$v.objNewReelSize.size.checkDuplicateSize">ReelSize already exist</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-md-3 mt-3">
                                                    <label class="switch">
                                                        <input type="checkbox" v-model="objNewReelSize.canMfOrder" />
                                                        <div class="slider round"></div>
                                                        <span>Visible to Manufacturare</span>
                                                    </label>
                                                </div>
                                                <div class="col-md-3 mt-3">
                                                    <label class="switch">
                                                        <input type="checkbox" v-model="objNewReelSize.canTpsafOrder" />
                                                        <div class="slider round"></div>
                                                        <span>Visible to TPSAF</span>
                                                    </label>
                                                </div>
                                                <div class="col-sm-2 mt-3 mb-3 p-0 text-right">
                                                    <img type="button" src="@/assets/images/icons/tick.png" v-on:click="addNewReelSizeforProductEditing(product);" />
                                                    <img type="button" src="@/assets/images/icons/cross-red.png" v-b-toggle.reel1 />
                                                </div>
                                            </div>
                                        </b-collapse>
                                        <hr class="dot" />
                                        <h6 class="mb-4">
                                            SKU <button class="btn float-right btn-link" type="button" v-on:click="addNewSKuErrorsforProduct=false" v-b-toggle.sku1><img src="@/assets/images/icons/add-small.png" />SKU</button>
                                        </h6>
                                        <div class="col-md-12" id="skuforEdit" v-for="sku in getAllsKUforEditProduct" :key="'eS' +sku.unit">
                                            <div class="col-sm-2 float-left">
                                                <label class="switch">
                                                    <input type="checkbox" v-model="sku.isActive" />
                                                    <div class="slider round"></div>
                                                    <span>{{sku.unit}}</span>
                                                </label>
                                            </div>
                                        </div>
                                        <div class="clearfix"></div>
                                        <b-collapse id="sku1" v-model="isVisiblesku1" class="mt-2">
                                            <div class="form-row mt-4 green-col">
                                                <div class="form-group row mt-3 mb-3 col-md-5">
                                                    <label class="p-2 col-form-label text-dark">Description</label>
                                                    <div class="col-sm-9">														
                                                        <div class="form-group" :class="{ 'form-group--error': $v.objNewSKU.description.$error }">
															<textarea id="newDesc" placeholder="Description" rows="3" class="form-control" v-model="objNewSKU.description"></textarea>
                                                            <p v-if="addNewSKuErrorsforProduct"> <span class="text-danger" v-if="!$v.objNewSKU.description.required">Description is required</span></p>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="form-group row mt-3 mb-3 col-md-5">
                                                    <label class="p-2 col-form-label text-dark">Unit</label>
                                                    <div class="col-sm-9">
                                                        <div class="form-group" :class="{ 'form-group--error': $v.objNewSKU.unit.$error }">
                                                            <input type="text" class="form-control" placeholder="Unit" v-model="objNewSKU.unit" />
                                                            <p v-if="addNewSKuErrorsforProduct">
                                                                <span class="text-danger" v-if="!$v.objNewSKU.unit.required">Unit is required</span>
                                                                <span class="text-danger" v-else-if="!$v.objNewSKU.unit.checkDuplicateUnit">Unit already exist</span>
                                                            </p>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-md-2 mt-3 text-right">
                                                    <img src="@/assets/images/icons/tick.png" type="button" v-on:click="addNewsKUforProductEditing(product);" />
                                                    <img src="@/assets/images/icons/cross-red.png" type="button" v-b-toggle.sku1 />
                                                </div>
                                            </div>
                                        </b-collapse>
                                        <hr class="dot" />
                                        <h6>
                                            Stamp Price <button class="btn float-right btn-link" type="button" v-if="(product.priceHistories.filter(element => element.status == 2)).length == 0" v-on:click="addNewPriceErrorsforProduct=false" v-b-toggle.price1><img src="@/assets/images/icons/add-small.png" />PRICE</button>
                                        </h6>
                                        <div class="form-row mt-4" v-if="product.priceHistories.length>0">
                                            <div class="table-responsive">
                                                <table class="table layout-secondary bordered">
                                                    <thead>
                                                        <tr>
                                                            <th>Stamp Price in GH</th>
                                                            <th>Start Date</th>
                                                            <th>End Date</th>
                                                            <th>Status</th>
                                                            <th>Action</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-for="prices in product.priceHistories" :key="'eP' + prices.id">
                                                            <td scope="row">{{prices.price}}</td>
                                                            <td>{{prices.effectiveDate | FormatingDate }}</td>
                                                            <td>{{prices.endDate}}</td>
                                                            <td class="text-success" v-if="prices.status==1">Active</td>
                                                            <td class="text-primary" v-else-if="prices.status==2">Enqueued</td>
                                                            <td class="text-danger" v-else>Obsolete</td>
                                                            <td>
                                                                <button v-show="prices.status==2" class="btn btn-link" type="button" @click="editPricesforProductEditing(prices)"><img src="@/assets/images/icons/edit-gray.png" /> Edit Price</button>
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                        <b-collapse id="price1" v-model="isVisibleprice1" class="mb-4">
                                            <div class="form-row green-col">
                                                <div class="form-group row mt-3 col-md-4">
                                                    <label class="p-2 col-form-label text-dark">Price</label>
                                                    <div class="col-sm-8">
														<div class="input-group" :class="{ 'form-group--error': $v.newPrice.$error }">
															<input type="number" class="form-control" id="newPrice" placeholder="Price" v-model.number="objNewPrices.price" />
															<div class="input-group-append">
																<div class="input-group-text">GH</div>
															</div>
															<p v-if="addNewPriceErrorsforProduct">
                                                                <span class="text-danger" v-if="!$v.objNewPrices.price.required">Price is required</span>
                                                                <span class="text-danger" v-else-if="!$v.objNewPrices.price.checkValid">This price should be above 0</span>
                                                                <span class="text-danger" v-else-if="!$v.objNewPrices.price.checkDuplicateDate">This price already in active, Enter different price</span>
                                                            </p>
														</div>
                                                    </div>
                                                </div>
                                                <div class="form-group row mt-3 mb-3 col-md-4">
                                                    <label class="p-2 col-form-label text-dark">Start Date</label>
                                                    <div class="col-md-8 p-0">
                                                        <div class="form-group" :class="{ 'form-group--error': $v.objNewPrices.effectiveDate.$error }">
															<b-form-datepicker right v-model="objNewPrices.effectiveDate" placeholder="MM-DD-YYYY" :min="minDate" :date-format-options="{ year: 'numeric', month: 'numeric', day: 'numeric'}" locale="en"></b-form-datepicker>
                                                            <p v-if="addNewPriceErrorsforProduct"><span class="text-danger" v-if="!$v.objNewPrices.effectiveDate.required">Start Date is required</span></p>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-md-4 mt-3 text-right">
                                                    <img src="@/assets/images/icons/tick.png" type="button" v-on:click="addNewPricesforProductEditing(product);" />
                                                    <img src="@/assets/images/icons/cross-red.png" type="button" v-b-toggle.price1 />
                                                </div>
                                            </div>
                                        </b-collapse>
                                        <div class="col-md-12 save-button">
                                            <button class="btn btn-primary btn-lg float-right mb-4" name="save" value="SAVE" type="button" @click="updateProductDto(product)" v-b-toggle="'accordion-' + product.id">UPDATE</button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </b-card-body>
                    </b-collapse>
                </b-card>
            </div>
            <!-- END: Card DATA-->
        </div>
    </div>
</template>
<script>
	import toast from '@/toast';
    import { required, minLength } from 'vuelidate/lib/validators'
    const { BlobServiceClient } = require("@azure/storage-blob");
    require('vue-select-image/dist/vue-select-image.css')
    export default {
        data() {
            const now = new Date();
            const minDate = new Date(now.getFullYear(), now.getMonth(), now.getDate());
            minDate.setDate(minDate.getDate() + 1);
            return {
                //Edit Product
                minDate: minDate, objNewReelSize: {}, objNewSKU: {}, objNewPrices: {}, arrSelectedProduct: [],
                arrReelsListforEditProduct: [], arrSKUListforEditProduct: [], addNewReelSizeErrorsforProduct: false,
                addNewSKuErrorsforProduct: false, addNewPriceErrorsforProduct: false,isVisiblereel1:false,isVisiblesku1:false,isVisibleprice1:false,
                //Add New Product
                productAddition: { newProductName: '', selectedOrgin: '', selectedCategory: {}, arrPriceList: [], },
                addProductErros: false, addReelSizeErrors: false, addSKuErrors: false, addPriceErrors: false,
                arrReelSizesList: [], arrSKUsList: [], newReelSize: NaN, newUnit: '', newDescribtion: '', newPrice: 0,
                newEffectiveDate: '', isAddPriceEnabled: true, dataImages:[], imageSelected:{src:'',name:''},
            }
        },
        filters: {
          FormatingDate: function (value) {
              if(value==null || value=='') return '';
              var date=new Date(value);
              var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
              return ('0' + date.getDate()).slice(-2) + "-" + months[date.getMonth()]  + "-" + date.getFullYear();
            }
        },
        validations: {
            productAddition: {
                newProductName: {
                    required,
                    minLength: minLength(2),
                    // checkDuplicateName(value) {
                    //     if (this.$store.state.productsDto.filter(element => element.name.toUpperCase() === value.toUpperCase()).length == 0) return true;
                    //     return false;
                    // }
                },
                selectedOrgin: {
                    required,
                    checkValid(value) {
                        if (value == "Select Orgin") return false
                        return true;
                    }
                },
                selectedCategory: {
                    required,
                    checkValid(value) {
                        if (value == "Select Category") return false
                        return true;
                    }
                }
            },
            newReelSize: {
                required,
                checkNaN(value){
                    if(isNaN(value)) return false
                    return true;
                },
                checkValid(value) {
                    if (value < 2500) return false
                    return true;
                },
                checkDuplicateSize(value) {
                    if (this.$store.state.reelSizesDto.filter(element => element.size === value).length === 0) return true;
                    return false;
                }
            },
            newDescribtion: { required },
            newUnit: {
                required,
                checkDuplicateUnit(value) {
                    if (this.$store.state.sKUsDto.filter(element => element.unit === value).length === 0) return true;
                    return false;
                }
            },
            newPrice: {
                required,
                checkValid(value) {
                    if (value <= 0)
                        return false;
                    return true;
                }
            },
            newEffectiveDate: { required },
            //Edit Product
            objNewReelSize: {
                size: {
                    required,
                    checkNaN(value){
                    if(isNaN(value)) return false
                    return true;
                    },
                    checkValid(value) {
                        if (value < 2500) return false
                        return true;
                    },
                    checkDuplicateSize(value) {
                        if (this.$store.state.reelSizesDto.filter(element => element.size === value).length === 0) return true;
                        return false;
                    }
                }
            },
            objNewSKU: {
                description: { required },
                unit: {
                    required,
                    checkDuplicateUnit(value) {
                        if (this.$store.state.sKUsDto.filter(element => element.unit === value).length === 0) return true;
                        return false;
                    }
                }
            },
            objNewPrices: {
                effectiveDate: { required },
                price: {
                    required,
                    checkValid(value) {
                        if (value <= 0)
                            return false;
                        return true;
                    },
                    checkDuplicateDate(value) {
                        if(this.arrSelectedProduct.length==0) return true;
                        if (this.arrSelectedProduct.priceHistories.filter(element => element.price === value && element.status === 1).length === 0) return true;
                        return false;
                    }
                }
            }
        },
        mounted() {
            //Edit Product
            this.$store.dispatch("getAllProductsDtoAsync");
            //Add New Product
            this.$store.dispatch("getAllReelSizesDtoAsync");
            this.$store.dispatch("getAllSKUsDtoAsync");
            this.$store.dispatch("getAllOrginsAsync");
            this.$store.dispatch("getAllCategoriesAsync");
            this.getImages();
        },
        computed: {
            //Edit New Product
            getAllProducts() { return this.$store.getters.productsDto; },
            getAllReelsforEditProduct() { return this.loadAllReelsforProductEditing(); },
            getAllsKUforEditProduct() { return this.loadAllsKUforProductEditing(); },
            //Add New Product
            getAllReelSizes() { return this.loadReelSizes(); },
            getAllSKUs() { return this.loadSKUs(); },
        },
        watch: {
            getAllProducts() {
                this.$store.dispatch("getAllReelSizesDtoAsync");
                this.$store.dispatch("getAllSKUsDtoAsync");
            }
        },
        methods: {
            loadNewProductScreen() {
                this.productAddition.arrPriceList = []; this.arrSKUsList = []; this.arrReelSizesList = []; this.isAddPriceEnabled = true, this.addProductErros = false;this.productAddition.newProductName='',this.productAddition.selectedOrgin='',this.productAddition.selectedCategory=null,this.imageSelected={}, this.addImageErros=true;
                this.$root.$emit('bv::show::modal', 'addNewProduct');
            },
            getImageUrl(imageName){
                return process.env.VUE_APP_BlobStorageBaseUrl +"/"+process.env.VUE_APP_BlobStorageProductImageContainer+"/" + imageName
            },
            toggleAccordianBasedOnProduct(product) {
                this.arrSelectedProduct = product;
                this.loadNewAssociatesforProduct(product);
                this.isVisiblereel1=false,
                this.isVisiblesku1=false,
                this.isVisibleprice1=false,
                this.$root.$emit('bv::toggle::collapse', 'accordion-' + product.id);
				var activeHeader = document.getElementById('header-' + product.id);
				var classHeader = activeHeader.classList.contains('not-collapsed');
				if(!classHeader) {
					var allHeaders = document.getElementsByClassName('card-header');
					for(var i=0; i< allHeaders.length; i++){
						allHeaders[i].classList.remove('not-collapsed');
					}
					activeHeader.classList.add('not-collapsed');
				} else {
					activeHeader.classList.remove('not-collapsed');					
				}
            },
            loadNewAssociatesforProduct(product) {
                this.objNewReelSize = { id: 0, productId: product.id, reelSizeId: 0, size: NaN, isActive: true, canMfOrder: false, canTpsafOrder: false };
                this.objNewSKU = { id: 0, productId: product.id, stockKeepingUnitId: 0, unit: '', description: '', isActive: true };
                this.objNewPrices = { id: 0, price: '', status: 2, effectiveDate: '', endDate: null };
            },
            loadAllReelsforProductEditing() {
                this.arrReelsListforEditProduct = [];
                this.$store.getters.activeReelSizesDto.forEach(reel => {
                    var objReel = { id: 0, productId: 0, reelSizeId: reel.id, size: reel.size, isActive: false, canMfOrder: false, canTpsafOrder: false };
                    this.arrReelsListforEditProduct.push(objReel);
                });
                if (this.arrSelectedProduct.length != 0)
                    return this.loadAllReelsforProductEditingById(this.arrSelectedProduct);
                else
                    return this.arrReelsListforEditProduct;
            },
            loadAllReelsforProductEditingById(product) {
                let prodReelstemp = this.$store.getters.activeReelSizesDto.filter((element) => element.productReelSizeDtos.
                    some((prodReel) => prodReel.productId === product.id)).
                    map(element => { let reels = Object.assign([], element); return reels.productReelSizeDtos.filter(prodReel => prodReel.productId === product.id); });
                let prodReels = [];
                prodReelstemp.forEach(element => { prodReels.push(element[0]); });
                this.arrReelsListforEditProduct.forEach(reel1 => {
                    prodReels.forEach(reel2 => {
                        if (reel1.size == reel2.size) { reel1.id = reel2.id; reel1.productId = reel2.productId; reel1.isActive = reel2.isActive; reel1.canMfOrder = reel2.canMfOrder; reel1.canTpsafOrder = reel2.canTpsafOrder; }
                    });
                });
                return this.arrReelsListforEditProduct;
            },
            loadAllsKUforProductEditing() {
                this.arrSKUListforEditProduct = [];
                this.$store.getters.activeSKUsDto.forEach(sku => {
                    var objReel = { id: 0, productId: 0, stockKeepingUnitId: sku.id, unit: sku.unit, description: sku.description, isActive: false };
                    this.arrSKUListforEditProduct.push(objReel);
                });
                if (this.arrSelectedProduct.length != 0)
                    return this.loadAllsKUforProductEditingById(this.arrSelectedProduct);
                else
                    return this.arrSKUListforEditProduct;
            },
            loadAllsKUforProductEditingById(product) {
                let prodSKUstemp = this.$store.getters.activeSKUsDto.filter((element) => element.productSkuDtos.
                    some((prodSku) => prodSku.productId === product.id)).
                    map(element => { let skus = Object.assign([], element); return skus.productSkuDtos.filter(prodSku => prodSku.productId === product.id); });
                let prodSKUs = [];
                prodSKUstemp.forEach(element => { prodSKUs.push(element[0]); });
                this.arrSKUListforEditProduct.forEach(sku1 => {
                    prodSKUs.forEach(sku2 => {
                        if (sku1.unit == sku2.unit) { sku1.id = sku2.id; sku1.productId = sku2.productId; sku1.isActive = sku2.isActive; sku1.description = sku2.description; }
                    });
                });
                return this.arrSKUListforEditProduct;
            },
            async addNewReelSizeforProductEditing(product) {
                this.$v.$touch();
                this.addNewReelSizeErrorsforProduct = this.$v.objNewReelSize.size.$anyError;
                if (!this.addNewReelSizeErrorsforProduct) {
                    let objReelSize = { id: 0, Size: this.objNewReelSize.size, isActive: true, productReelSizeDtos: [] };
                    objReelSize.productReelSizeDtos.push(this.objNewReelSize);
                    await this.$store.dispatch("AddUpdateReelSizeDtoAsync", objReelSize);
                    await toast.success("Coil Added!..");
                    await this.loadNewAssociatesforProduct(product);
                }
            },
            async addNewsKUforProductEditing(product) {
                this.$v.$touch();
                this.addNewSKuErrorsforProduct = this.$v.objNewSKU.unit.$anyError || this.$v.objNewSKU.description.$anyError;
                if (!this.addNewSKuErrorsforProduct) {
                    let objSKU = { id: 0, unit: this.objNewSKU.unit, description: this.objNewSKU.description, isActive: true, productSkuDtos: [] };
                    objSKU.productSkuDtos.push(this.objNewReelSize);
                    await this.$store.dispatch("AddUpdateSKUDtoAsync", objSKU);
                    await toast.success("Measurement unit Added!..");
                    await this.loadNewAssociatesforProduct(product);
                }
            },
            async addNewPricesforProductEditing(product) {
                this.$v.$touch();
                this.addNewPriceErrorsforProduct = this.$v.objNewPrices.effectiveDate.$anyError || this.$v.objNewPrices.price.$anyError;
                if (!this.addNewPriceErrorsforProduct) {
                    let objProd = {
                        id: product.id, name: product.name, origin: product.origin, imageName:product.imageName,
                        category: { id: product.category.id, name: product.category.name }, reelSizes: [], skus: [], priceHistories: []
                    };
                    objProd.priceHistories.push(this.objNewPrices);
                    this.$root.$emit('bv::toggle::collapse', 'price1');
                    await this.$store.dispatch("AddUpdateProductDtoAsync", objProd);
                    await toast.success("New price Added!..");
                    await this.loadNewAssociatesforProduct(product);
                }
            },
            editPricesforProductEditing(prices) {
                this.$root.$emit('bv::toggle::collapse', 'price1');
                this.objNewPrices = { id: prices.id, price: prices.price, status: 2, effectiveDate: prices.effectiveDate, endDate: null };
            },
            async updateProductDto(product) {
                let objProd = {
                    id: product.id, name: product.name, origin: product.origin, imageName:product.imageName,
                    category: { id: product.category.id, name: product.category.name }, reelSizes: [], skus: [], priceHistories: []
                };
                this.arrReelsListforEditProduct.forEach(prodReel => {
                    if (!(prodReel.id == 0 && prodReel.isActive == false)) { objProd.reelSizes.push(prodReel); }
                });
                this.arrSKUListforEditProduct.forEach(ProdSKU => {
                    if (!(ProdSKU.id == 0 && ProdSKU.isActive == false)) { objProd.skus.push(ProdSKU); }
                });
                await this.$store.dispatch("AddUpdateProductDtoAsync", objProd);
                await toast.success("Product Updated!..");
            },
            //Add New Product
            loadReelSizes() {
                this.addReelSizeErrors = false;
                this.arrReelSizesList = [];
                this.$store.getters.activeReelSizesDto.forEach(reel => {
                    var objReels = { isReelSizeSelected: false, reelSizeId: reel.id, size: reel.size, canMfOrder: false, canTpsafOrder: false };
                    this.arrReelSizesList.push(objReels);
                });
                return this.arrReelSizesList;
            },
            loadSKUs() {
                this.addSKuErrors = false;
                this.arrSKUsList = [];
                this.$store.getters.activeSKUsDto.forEach(sku => {
                    var objSKU = { isSKUSelected: false, SKUid: sku.id, unit: sku.unit, description: sku.description };
                    this.arrSKUsList.push(objSKU);
                });
                return this.arrSKUsList;
            },
            async addNewReelSize() {
                this.$v.$touch();
                this.addReelSizeErrors = this.$v.newReelSize.$anyError;
                if (!this.addReelSizeErrors) {
                    var objReel = { id: 0, size: this.newReelSize, isActive: true, productReelSizeDtos: [] };
                    await this.$store.dispatch("AddUpdateReelSizeDtoAsync", objReel);
                    await toast.success("Coil Added!..");
                    this.newReelSize = NaN;
                }
            },
            async addNewSKU() {
                this.$v.$touch();
                this.addSKuErrors = this.$v.newUnit.$anyError || this.$v.newDescribtion.$anyError;
                if (!this.addSKuErrors) {
                    var objSKU = { id: 0, unit: this.newUnit, description: this.newDescribtion, isActive: true, productSkuDtos: [] };
                    await this.$store.dispatch("AddUpdateSKUDtoAsync", objSKU);
                    await toast.success("Measurement unit Added!..");
                    this.newUnit = this.newDescribtion = '';
                }
            },
            addNewPrice() {
                this.$v.$touch();
                this.addPriceErrors = this.$v.newPrice.$anyError || this.$v.newEffectiveDate.$anyError;
                if (!this.addPriceErrors) {
                    var objPrice = {
                        id: 0, price: parseFloat(this.newPrice),
                        status: 2, effectiveDate: this.newEffectiveDate, endDate: null
                    };
                    this.productAddition.arrPriceList.push(objPrice);
                    if (this.productAddition.arrPriceList.length != 0 && this.productAddition.arrPriceList.filter(element => element.status == 2))
                        this.isAddPriceEnabled = false;
                    else
                        this.isAddPriceEnabled = true;
                    this.newPrice = 0;
                    this.newEffectiveDate = '';
                    this.$root.$emit('bv::toggle::collapse', 'addprice1');
                }
            },
            async addNewProduct() {
                this.$v.$touch();
                this.addProductErros = this.$v.productAddition.$anyError;
                if (!this.addProductErros){
                    var objProd = {
                        id: 0, name: this.productAddition.newProductName, imageName : this.imageSelected.name,
                        origin: this.productAddition.selectedOrgin == "Domestic" ? 1 : 2,
                        category: this.productAddition.selectedCategory, reelSizes: [],
                        skus: [], priceHistories: []
                    };
                    let associatedReelSize = this.arrReelSizesList.filter(reel => reel.isReelSizeSelected == true);
                    associatedReelSize.forEach(prodReel => {
                        var reel = { id: 0, productId: 0, reelSizeId: prodReel.reelSizeId, size: prodReel.size, isActive: true, canMfOrder: prodReel.canMfOrder, canTpsafOrder: prodReel.canTpsafOrder };
                        objProd.reelSizes.push(reel);
                    });
                    let associatedSKU = this.arrSKUsList.filter(sku => sku.isSKUSelected == true);
                    associatedSKU.forEach(prodSKU => {
                        var sKU = { id: 0, productId: 0, stockKeepingUnitId: prodSKU.SKUid, unit: prodSKU.unit, description: prodSKU.description, isActive: true };
                        objProd.skus.push(sKU);
                    });
                    objProd.priceHistories = this.productAddition.arrPriceList;
                    this.$root.$emit('bv::hide::modal', 'addNewProduct');
                    await this.$store.dispatch("AddUpdateProductDtoAsync", objProd);
                    await toast.success("Product Added!..");
                }
            },
            async getImages(){
                const blobServiceClient = new BlobServiceClient(
                    process.env.VUE_APP_BlobStorageBaseUrl
                );
                const containerBaseClient = blobServiceClient.getContainerClient(process.env.VUE_APP_BlobStorageProductImageContainer);
                let blobs = containerBaseClient.listBlobsFlat();

                for await (const blob of blobs){
                    this.dataImages.push({src : process.env.VUE_APP_BlobStorageBaseUrl +"/"+process.env.VUE_APP_BlobStorageProductImageContainer+"/" + blob.name, name : blob.name,});
                }
            },
             onSelectImage: function (image) {
                this.imageSelected = image;
                // this.addImageErros = false;
    },
        }
    }
</script>
